<template>
  <div class="actual-form">
    <Crumbs
      :parent-manage="'合同管理'"
      :children-manage="'执行情况'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <!-- 筛选内容 -->
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="85"
      >
        <Row>
          <Col span="11">
          <FormItem label="合同名称">
            <Input v-model="searchData.contractName" placeholder="请填写合同名称" clearable /></Input>
          </FormItem>
          </Col>
          <Col span="11" offset="2" style="text-align: right;">
          <Button type="primary" @click="searchList">确认</Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <!-- /筛选内容 -->

    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button icon="md-print" @click.stop="printContent(22)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/contract/exportImplementationListExcel')">
        导出
      </Button>
      <Table
        ref="tableContent"
        highlight-row
        class="table"
        :columns="columns"
        :data="implementationModelList"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
    </Content>
    <!-- 弹出框 -->
    <construction-model ref="constructionModel" :contract-id="contractId" :sign-gist="signGist" :contract-attr="contractAttr" :budget-gist="budgetGist" is-disabled @refreshList="searchList"></construction-model>
    <setUpModel ref="setUpModel" :contract-id="contractId" :sign-gist="signGist" :contract-attr="contractAttr" :budget-gist="budgetGist" is-disabled @refreshList="searchList"></setUpModel>
    <entrustModel ref="entrustModel" :contract-id="contractId" :sign-gist="signGist" :contract-attr="contractAttr" :budget-gist="budgetGist" is-disabled @refreshList="searchList"></entrustModel>
    <!--审核的弹出框-->
    <audit ref="audit" :form="auditForm" />
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';
import constructionModel from './components/construction';
import setUpModel from './components/setUp';
import entrustModel from './components/entrust';
import tablePage from '@/mixins/tablePage.js';
import audit from '@/components/audit.vue'
export default {
  name: 'ActualForm',
  components: { Crumbs, constructionModel, setUpModel, entrustModel, audit },
  mixins: [tablePage],
  data() {
    return {
      contractId: null,
      // 签订依据
      signGist: {
        1: '比选',
        2: '招标',
        3: '其他'
      },
      // 预算依据
      budgetGist: {},
      // 合同属性
      contractAttr: {
        1: '新签合同',
        2: '补充合同'
      },
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 合同类型
      contractTypeList: {
        1: {
          modal: 'constructionModel',
          name: '建设工程施工合同'
        },
        2: {
          modal: 'setUpModel',
          name: '设备购置合同'
        },
        3: {
          modal: 'entrustModel',
          name: '委托业务合同'
        }
      },
      // 筛选内容_字段
      searchData: {
        // 合同名称
        contractName: '',
        ascending: false,
        orderBy: '',
        pageNo: 1,
        pageSize: 10
      },
      // 表格内容 字段相对应
      implementationModelList: [],
      listUrl: '/contract/searchImplementation',
      // 乙方单位
      yifangList: [],
      columns: [
        {
          title: '合同名称',
          key: 'contractName',
          render: (h, params) => {
            const row = params.row
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  const form = Object.assign({}, row);
                  const name = this.contractTypeList[form.contractType].modal
                  this.contractId = form.contractId
                  this.$nextTick(() => {
                    this.isDisabled = true;
                    this.$refs[name].showModal(form.contractType);
                  })
                }
              }
            }, row.contractName);
          }
        },
        {
          title: '总额',
          key: 'allMoney'
        },
        {
          title: '应付',
          key: 'sumYinfu'
        },
        {
          title: '实付',
          key: 'sumShifu'
        },
        {
          title: '发票金额',
          key: 'billMoney'
        },
        {
          title: '支付比例',
          key: 'scale'
        }
      ]
    }
  },
  created() {
    this.getYiFangInfo()
    this.getBudgetGist()
    this.searchList()
  },
  methods: {
    searchList() {
      const that = this;
      that.search(that.getNewSearchData(), (flag, res) => {
        if (flag) {
          const data = res.data
          that.implementationModelList = data.implementationModelList
          that.total = data.totalCount
        }
      })
    },
    getBudgetGist() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/contract/searchBudgetGist', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.budgetGist = res.data
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 获取乙方信息列表
    getYiFangInfo() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/contract/searchYiFang', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.yifangList = res.data
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 取消操作
    handleCancel() { },
    // 确认操作   关系后台数据
    handleConfirm() { }
  }
}
</script>

<style lang="scss" scoped></style>



